.restaurants {
  grid-gap: 1em;
  grid-template-columns: repeat(auto-fill, min(400px, 90vw));
  grid-auto-rows: 200px;
  align-content: center;
  justify-content: center;
  margin: 2em;
  display: grid;
}

.restaurants .restaurant {
  cursor: pointer;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 10px;
  position: relative;
}

.restaurants .restaurant h1 {
  color: #000;
  text-align: center;
  width: 100%;
  background-color: rgba(255, 255, 255, .667);
  border-radius: 0 0 10px 10px;
  margin: 0;
  padding: .25em 0;
  font-size: 1.75rem;
  position: absolute;
  bottom: 0;
}

.restaurants .restaurant:hover {
  box-shadow: 0 0 6px 2px #0f0f0f;
}

